<template>
    <div class="orderPayNew" :class='isShowCoverUp?"fiexedClass":""' can-swipe-back="false" ref="orderPayNew">
        <!-- <van-nav-bar class="task-nav-bar" title="订单支付" :border="false" left-arrow @click-left="goBack" /> -->
        <!-- 头部 -->
        <navigation-bar @onLeftClick="goBack" pageName="订单支付" />
        <div class="orderPayNewContent" :class="{themeL:bindThemeL}">
            <div class="orderPayNewScroll" ref="orderPayNewScroll" :class="{stopScrollStyle :isShowCoverUp}">
                <div class="addRessBox">
                    <div class="selectBtn">
                        <div :class="!isSelectBool?'actSelectBtn':'noSelectBtn'" @click="switchBtn(false)">自提</div>
                        <div :class="isSelectBool?'actSelectBtn':'noSelectBtnRight'" @click="switchBtn(true)">配送</div>
                    </div>
                    <div class="selfMentionBox" v-if="isSelectBool">
                        <div class="selfMention">
                            <div>
                                <input type="text" class="pickupName" maxlength="8" style="width:100%"
                                       placeholder="请输入联系人" v-model="recName" @focus="pickupFocus" @blur="pickupBlur">
                            </div>
                            <div>
                                <input type="tel" class="pickupPhone" placeholder="请输入联系电话" v-model="recPhone"
                                       oninput="if(value.length>11)value=value.slice(0,11)"
                                       onkeyup="value=value.replace(/[^\d]/g,'')" @focus="pickupFocus"
                                       @blur="pickupBlur" />
                            </div>
                        </div>
                        <div class="lineBox" v-if="recAddr"></div>
                        <div class="consigneeAddressBox">
                            <div class="consigneeLeftBox" v-if="!recAddr">
                                <div class="hasConsigneeAddressBox">
                                    <div class="consigneeInfo reginInfo" v-if="!recAddr">
                                        <div class="regionRight">
                                            <label for="region">所在地区：</label>
                                            <div class="reginLeftTxt"
                                                 v-if="!isVirtualArea||(isVirtualArea&&!regionData.recDistrict)"
                                                 @click="showSelectBox()">
                                                {{defaultRegionData.recAreaName}}</div>
                                            <div class="reginLeftTxt" v-if="isVirtualArea&&regionData.recDistrict"
                                                 @click="showSelectBox()">
                                                {{regionData.recProvince}} {{regionData.recCity}}
                                                {{regionData.recDistrict}}
                                            </div>
                                        </div>
                                        <img src="@/assets/images/home/property_payment/icon_arrow.png" alt="">

                                    </div>
                                    <div class="consigneeInfo" v-if="!recAddr">
                                        <label for="site">详细地址：</label>
                                        <input id="site" placeholder="街道、楼牌号等" type="text" v-model="recAddressDetail"
                                               maxlength="50">
                                    </div>
                                </div>
                            </div>
                            <div class="consigneeAddressBox" v-if="recAddr" @click="goRecAddrList()">
                                <img class="consigneePosition" src="@/assets/images/mall/icon_position.png">
                                <div class="recAddress">
                                    <div class="recAddressTxt">
                                        地址：{{recAddr.recAddr}}
                                    </div>
                                </div>
                                <img class="consigneeArrow" src="@/assets/images/home/property_payment/icon_arrow.png">
                            </div>

                        </div>
                        <img class="bottomRibbon" src="@/assets/images/mall/order_address_top_border.png">
                    </div>
                    <div class="selfMentionBox" v-if="!isSelectBool">
                        <div class="selfMention">
                            <div>
                                <input type="text" class="pickupName" maxlength="8" style="width:100%"
                                       placeholder="请输入联系人" v-model="ownerName" @focus="pickupFocus" @blur="pickupBlur">
                            </div>
                            <div>
                                <input type="tel" class="pickupPhone" placeholder="请输入联系电话" v-model="ownerPhone"
                                       oninput="if(value.length>11)value=value.slice(0,11)"
                                       onkeyup="value=value.replace(/[^\d]/g,'')" @focus="pickupFocus"
                                       @blur="pickupBlur" />
                            </div>
                        </div>
                        <div class="lineBox"></div>
                        <div class="consigneeAddressBox">
                            <div class="consigneeLeftBoxSelfMention">
                                <img class="consigneePosition" src="@/assets/images/mall/icon_position.png">
                                <div class="pickUpAddress">
                                    <div class="nameAndPhoneBox">
                                        <div class="consigneeName">{{pickup.pickupName}}</div>
                                        <div class="consigneePhone">{{pickup.pickupPhone}}</div>
                                    </div>
                                    <div class="consigneeAddress">地址：{{pickup.pickupAddr}}</div>
                                </div>
                            </div>
                        </div>
                        <img class="bottomRibbon" src="@/assets/images/mall/order_address_top_border.png">
                    </div>
                </div>
                <div class="payBody">
                    <div class="storeList" v-if="isSelectBool" v-for="(shopItem,i) in shopList " :key="'shopItem'+i">
                        <div class="storeNameBox">
                            <div class="storeName">{{shopItem.shopName}}</div>
                        </div>
                        <div class="goodsListBox">
                            <div class="goodsList" v-for="(skuItem,i) in shopItem.skuList" :key="'skuItem'+i">
                                <div class="goodsImgBox">
                                    <img class="goodsImg" :src="skuItem.skuPicUrl" alt="" :onerror="defaultImg">
                                    <div class="sellOut" v-if="skuItem.storeNumber==0&&!skuItem.activitySpuId">补货中</div>
                                    <div class="stockNum"
                                         v-if="skuItem.storeNumber<skuItem.skuCountLimit&&skuItem.storeNumber!=0">
                                        仅剩{{skuItem.storeNumber}}件</div>
                                </div>
                                <div class="goodsInfoBox">
                                    <div class="goodsNameAndPrice">
                                        <div class="lightGoodsName" v-if="skuItem.noDistribution">
                                            {{skuItem.spuName}}</div>
                                        <div class="goodsName" v-if="!skuItem.noDistribution">
                                            {{skuItem.spuName}}</div>
                                        <div class="goodsPrice">¥{{skuItem.skuPrice.toFixed(2)}}</div>
                                    </div>
                                    <div class="noDistribution" v-if="skuItem.noDistribution">
                                        商品不在配送范围内，请重选收货地址！
                                    </div>
                                    <div class="goodsSpecificationsBox" v-if="!skuItem.noDistribution">
                                        <div class="goodsSpecifications">{{skuItem.attrValueContent}}</div>
                                        <div class="goodsNum">x{{skuItem.skuCount}}</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="remarkBox">
                            <div class="remarkTime"
                                 v-if="!shopItem.noDistribution&&shopItem.deliveryDayList&&shopItem.deliveryDayList.length>0">
                                <div class="remarkNameTime">送达时间</div>
                                <div class="deliveryNumTime" v-if="shopItem.deliveryNeedUpdata!=1"
                                     @click="deliveryClick(shopItem,i)">
                                    {{shopItem.deliveryDefaultString}}</div>
                                <div class="deliveryNumRedTime" v-if="shopItem.deliveryNeedUpdata==1"
                                     @click="deliveryClick(shopItem,i)">
                                    {{shopItem.deliveryDefaultString}}</div>
                                <img class="storeImg" src="@/assets/images/home/property_payment/more_black.png" alt=""
                                     @click="deliveryClick(shopItem,i)">
                            </div>
                            <div class="remark">
                                <div class="remarkName">配送费</div>
                                <div class="deliveryNum" v-if="!shopItem.noDistribution">
                                    {{shopItem.shopDeliveryTitle}}</div>
                                <div class="delivery" v-if="shopItem.noDistribution">
                                    超出配送范围</div>
                            </div>
                            <!-- <div class="remarkTime">
                                <div class="remarkNameTime">如遇缺货</div>
                                <div class="deliveryNumTime"
                                    @click="outClick(shopItem,i)">
                                    {{shopItem.nostoreString}}</div>
                                <img class="storeImg" src="@/assets/images/home/property_payment/more_black.png" alt=""
                                    @click="outClick(shopItem,i)">
                            </div> -->
                            <div class="remark">
                                <div class="remarkName">订单备注</div>
                                <textarea id="remarkTextarea" cols="15" rows="2" maxlength="30" placeholder="写写备注/要求"
                                          type="text" v-model="shopItem.remark"></textarea>
                            </div>
                        </div>
                        <div class="amountBox">
                            <div class="amountName">小计：</div>
                            <div class="amountNum">¥{{shopItem.shopTotal}}</div>
                        </div>
                    </div>
                    <div class="storeList" v-if="!isSelectBool" v-for="(shopItem,i) in pickupShopList "
                         :key="'shopItem'+i">
                        <div class="storeNameBox">
                            <div class="storeName">{{shopItem.shopName}}</div>
                        </div>
                        <div class="goodsListBox">
                            <div class="goodsList" v-for="(skuItem,i) in shopItem.skuList" :key="'skuItem'+i">
                                <div class="goodsImgBox">
                                    <img class="goodsImg" :src="skuItem.skuPicUrl" alt="" :onerror="defaultImg">
                                    <div class="sellOut" v-if="skuItem.storeNumber==0">补货中</div>
                                    <div class="stockNum"
                                         v-if="skuItem.storeNumber<skuItem.skuCountLimit&&skuItem.storeNumber!=0">
                                        仅剩{{skuItem.storeNumber}}件</div>
                                </div>
                                <div class="goodsInfoBox">
                                    <div class="goodsNameAndPrice">
                                        <div class="lightGoodsName" v-if="skuItem.noDistribution">
                                            {{skuItem.spuName}}</div>
                                        <div class="goodsName" v-if="!skuItem.noDistribution">
                                            {{skuItem.spuName}}</div>
                                        <div class="goodsPrice">¥{{skuItem.skuPrice.toFixed(2)}}</div>
                                    </div>
                                    <div class="noDistribution" v-if="skuItem.noDistribution">
                                        商品不在配送范围内，请重选收货地址！
                                    </div>
                                    <div class="goodsSpecificationsBox" v-if="!skuItem.noDistribution">
                                        <div class="goodsSpecifications">{{skuItem.attrValueContent}}</div>
                                        <div class="goodsNum">x{{skuItem.skuCount}}</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="remarkBox">
                            <div class="remarkTime"
                                 v-if="!shopItem.noDistribution&&shopItem.deliveryDayList&&shopItem.deliveryDayList.length>0">
                                <div class="remarkNameTime">送达时间</div>
                                <div class="deliveryNumTime" v-if="shopItem.deliveryNeedUpdata!=1"
                                     @click="deliveryClick(shopItem,i)">
                                    {{shopItem.deliveryDefaultString}}</div>
                                <div class="deliveryNumRedTime" v-if="shopItem.deliveryNeedUpdata==1"
                                     @click="deliveryClick(shopItem,i)">
                                    {{shopItem.deliveryDefaultString}}</div>
                                <img class="storeImg" src="@/assets/images/home/property_payment/more_black.png" alt=""
                                     @click="deliveryClick(shopItem,i)">
                            </div>

                            <div class="remark">
                                <div class="remarkName">配送费</div>
                                <div class="deliveryNum" v-if="!shopItem.noDistribution">
                                    {{shopItem.shopDeliveryTitle}}</div>
                                <div class="delivery" v-if="shopItem.noDistribution">
                                    超出配送范围</div>
                            </div>

                            <div class="remark">
                                <div class="remarkName">订单备注</div>
                                <textarea id="remarkTextarea" cols="15" rows="2" maxlength="30" placeholder="写写备注/要求"
                                          type="text" v-model="shopItem.remark"></textarea>
                            </div>
                        </div>
                        <div class="amountBox">
                            <div class="amountName">小计：</div>
                            <div class="amountNum">¥{{shopItem.shopTotal}}</div>
                        </div>
                    </div>
                </div>
                <div class="pointInfoBox">
                    <div class="nostoreBox">
                        <div class="userCouponTitle">如遇缺货</div>
                        <div class="userCouponInfo">
                            <div class="nocouponTxt" @click="outClick()">
                                {{nostoreString}}</div>
                            <img class="couponMoreImg" src="@/assets/images/home/property_payment/more_black.png" alt=""
                                 @click="outClick()">
                        </div>
                    </div>
                </div>
                <div class="pointInfoBox">
                    <div class="userCouponBox" @click="isShowCoverUpBtn()">
                        <div class="userCouponTitle">优惠券 <div class="userCouponImg"
                                                              style="font-size: 10px;height: 16px;line-height: 16px;border: 0.5px solid rgb(251, 87, 104);
                                padding-top: 0px;" v-show="coupon.isDefault == 1">
                            已使用推荐优惠
                        </div>
                        </div>
                        <div class="userCouponInfo"><span
                                v-if="coupon.couponTotal!=0">-¥{{(parseFloat(coupon.couponTotal)).toFixed(2)}}</span>
                            <span class="nocouponTxt" v-if="coupon.couponTotal==0&&isCouponBool">暂无可用优惠券</span><span
                                    class="nocouponTxt1"
                                    v-if="coupon.couponTotal==0&&!isCouponBool">有{{couponUsefulList.length}}张可用优惠券</span>
                            <img class="couponMoreImg" src="@/assets/images/home/property_payment/more_black.png"
                                 alt="">
                        </div>
                    </div>
                    <div class="pointDeduction">
                        <div class="change_point">
                            <img class="delete_icon" @click="numberReduce(pointAviable)"
                                 src="@/assets/images/mall/bg_cut.png" />
                            <input type="tel" onkeyup="value=value.replace(/[^\d]/g,'')" class="point_num"
                                   id="point_num" v-model="integralDeduction" @blur="changePointNum()"
                                   :disabled="!this.$userInfo.ROOM_CODE">
                            <img class="add_icon" @click="numberAdd(pointAviable)"
                                 src="@/assets/images/mall/bg_add.png" />
                        </div>
                        <div class="point_info">
                            <div class="point_title">积分抵扣</div>
                            <div class="point_rule" v-if="this.$userInfo.ROOM_CODE">100积分抵扣1元</div>
                            <div class="no_point_rule" v-if="!this.$userInfo.ROOM_CODE">绑定房产才可使用积分哦</div>
                        </div>
                    </div>
                    <div class="pointDeductionAll">
                        <van-switch v-model="integralAll" active-color="#FB5768" inactive-color="#D1D1D1" size="26px"
                                    @change="integralAllFn(pointAviable)" />

                        <div class="point_info">
                            <div class="point_title">
                                <span>使用全部积分抵扣</span>
                                <div class="point_title_div" @click="integralInstructionFn()">
                                    <img class="point_title_img"
                                         src="@/assets/images/home/property_payment/icon_tips.png">
                                </div>
                            </div>
                            <div class="point_rule">本次可用最高积分为{{pointAviable}}</div>
                        </div>
                    </div>
                </div>
                <div class="total_and_deduction">
                    <div class="total_money">
                        <div class="total_name">订单总额</div>
                        <div class="total_num">￥{{bill_amount.toFixed(2)}}</div>
                    </div>
                    <div class="deduction_point">
                        <div class="deduction_name">积分抵扣</div>
                        <div class="deduction_num">-￥{{pointAviablePersent}}</div>
                    </div>
                    <div class="deduction_point">
                        <div class="deduction_name">折扣优惠</div>
                        <div class="deduction_num">-￥{{(parseFloat(coupon.couponTotal)).toFixed(2)}}</div>
                    </div>
                </div>
                <div :style="placeholderStyle"></div>
                <div ref="placeHolder" style="height:200px" v-show="isShowPlaceHolder"></div>
            </div>
            <div class="payBox bar-footer" :style="{paddingBottom:this.$footBarHeight+'px'}">
                <div class="secksellMsg" v-show="hasSecksellGoods">秒杀商品不参与其他活动哦</div>
                <div class="actualPayBox">


                    <div class="actualPay">
                        <div class="actualName">实际支付：</div>
                        <div class="actualNum">￥{{billCodePoint |threshold}}</div>

                    </div>
                    <div class="payBtn" @click="goToPay()">立即支付</div>
                </div>
            </div>
        </div>
        <van-popup v-model="showPicker" position="bottom">
            <van-picker ref="deliveryPicker" show-toolbar :columns="columns" @cancel="showPicker = false"
                        @confirm="pickerOnConfirm" @change="pickerOnChange" confirm-button-text="确定" title="选择送达时间" />
        </van-popup>


        <van-popup v-model="showOutPicker" position="bottom">
            <van-picker ref="outPicker" show-toolbar :columns="outColumns" @cancel="showOutPicker = false"
                        @confirm="outPickerOnConfirm" confirm-button-text="确定" title="如遇缺货" />
        </van-popup>

        <van-popup v-model="payWindowShow" position="bottom" class="vanPopup" @close="closePopup">
            <div class="payModeBox">
                <div class="payModeTitle">支付方式</div>
                <div v-show="isAcppay" class="payModeItem" @click="alipay(2)">
                    <div class="unipayclass">
                        <img class="unipayIcon" src="@/assets/images/common/icon_unipay.png" alt="">
                        <div style="display: inline-block; vertical-align:middle;">云闪付</div>
                        <img class="iconUnipayRight" src="@/assets/images/common/icon_unipay_right.png" alt="" style="display: inline-block;vertical-align:middle;width: 21px;height: 21px;">
                        <div class="saleIcon" style="display: inline-block;vertical-align:middle;" v-if="isAcpPayTag">{{acpPayTag}}</div>
                    </div>
                </div>
                <div v-show="isAlipay" class="payModeItem" @click="alipay(1)">
                    <img src="@/assets/images/common/icon_alipay.png" alt="">
                    <div>支付宝</div>
                </div>
                <div v-show="isWechart" class="payModeItem" @click="alipay(0)">
                    <img src="@/assets/images/common/icon_wechart.png" alt="">
                    <div>微信</div>
                </div>
            </div>
            <div class="cancelBtn" @click="closePopup">取消</div>
        </van-popup>
        <div class="coverUpBox" v-if="isShowCoverUp" @click="hideCoverUp()"></div>
        <div class="couponBox" v-if="isShowCoverUp">
            <div class="couponHead">
                <div class="commonHead" @click="hideCoverUp()"><img src="@/assets/images/common/left_black.png">
                </div>
                <div class="title">优惠券</div>
            </div>
            <div class="btnList" style="position: absolute;width:100%;top: 45px;">
                <div class="btnItem" @click="setBtnItemIndex(0)">
                    <div class="btnItemTxt" :class="{'showItemTxt':btnItemIndex == 0}">
                        可用优惠券({{couponUsefulList.length}})
                    </div>
                    <div class="signBox" v-if="btnItemIndex == 0">
                        <span class="sign"></span>
                    </div>
                </div>
                <div class="btnItem" @click="setBtnItemIndex(1)">
                    <div class="btnItemTxt" :class="{'showItemTxt':btnItemIndex == 1}">
                        不可用优惠券({{couponUselessList.length}})</div>
                    <div class="signBox" v-if="btnItemIndex == 1">
                        <span class="sign"></span>
                    </div>
                </div>
            </div>
            <div class="selectContent yesSelectContent" has-bouncing='false' v-if="btnItemIndex == 0">
                <div class="couponList" v-if="couponUsefulList.length!=0">
                    <div class="couponItem" v-for="(couponUsefulItem,i)  in couponUsefulList"
                         :key="'couponUsefulItem'+i">
                        <div class="coupon">
                            <div class="couponInfo">
                                <div class="couponInfoBox">
                                    <div class="couponFirst">
                                        <div class="couponPriceBox">
                                            <div class="couponPriceNum">
                                                <span>￥</span>
                                                <span>{{couponUsefulItem.oldCouponTotal}}</span>
                                            </div>
                                            <div class="couponPriceTxt">
                                                <div>{{couponUsefulItem.shopName}}</div>
                                                <div>{{couponUsefulItem.name}}</div>
                                            </div>
                                        </div>

                                    </div>
                                    <div class="couponSec">
                                        <div class="timeTxt">
                                            {{couponUsefulItem.effBeginDay}}-{{couponUsefulItem.effEndDay}}</div>

                                        <div class="rangeBox" @click="changeSeeRange(i,0)">
                                            <div>适用范围</div>
                                            <img src="@/assets/images/mall/mall_arrow_down.png" alt=""
                                                 v-if="!couponUsefulItem.seeRange">
                                            <img src="@/assets/images/mall/mall_arrow_up.png" alt=""
                                                 v-if="couponUsefulItem.seeRange">
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="selectIcon" @click="selectCouponItem(i)">
                                <img v-if="selectCouponIndex != i" src='@/assets/images/common/check_default.png'
                                     alt="">
                                <img v-if="selectCouponIndex == i" src='@/assets/images/common/chebox_choice.png'
                                     alt="">
                            </div>
                        </div>
                        <div class="couponContent" v-if="couponUsefulItem.seeRange">
                            全品类通用
                        </div>
                    </div>
                </div>
                <div style="height: 50px" v-if="couponUsefulList.length!=0"></div>
                <div class="noDataBox" v-if="couponUsefulList.length==0">
                    <img class="noDataImg" src="@/assets/images/default/no_coupon_img.png" alt="">
                    <div class="noDataTxt">暂无优惠券</div>
                </div>
            </div>
            <div class="selectContent noSelectContent" has-bouncing='false' v-if="btnItemIndex == 1">
                <div class="couponList" v-if="couponUselessList.length!=0">
                    <div class="couponItem" v-for="(couponUselessItem,i)  in couponUselessList "
                         :key="'couponUselessItem'+i">
                        <div class="disableCouponInfo">
                            <div class="disableCouponInfoBox">
                                <div class="couponFirst">
                                    <div class="couponPriceBox">
                                        <div class="couponPriceNum">
                                            <span>￥</span>
                                            <span>{{couponUselessItem.oldCouponTotal}}</span>
                                        </div>
                                        <div class="couponPriceTxt">
                                            <div>{{couponUselessItem.shopName}}</div>
                                            <div>{{couponUselessItem.name}}</div>
                                        </div>
                                    </div>
                                    <div class="couponRight">
                                        <div class="receiveBtn" @click="goToHeadPage(couponUselessItem.shopCode)">
                                            去凑单
                                        </div>
                                    </div>
                                </div>
                                <div class="couponSec">
                                    <div class="timeTxt">
                                        {{couponUselessItem.effBeginDay}}-{{couponUselessItem.effEndDay}}</div>

                                    <div class="rangeBox" @click="changeSeeRange(i,1)">
                                        <div>适用范围</div>
                                        <img src="@/assets/images/mall/mall_arrow_down.png" alt=""
                                             v-if="!couponUselessItem.seeRange">
                                        <img src="@/assets/images/mall/mall_arrow_up.png" alt=""
                                             v-if="couponUselessItem.seeRange">
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="couponContent">
                            <div class="firstTxt" v-if="couponUselessItem.seeRange">
                                全品类通用
                            </div>
                            <div>
                                差{{couponUselessItem.restLimitTotal}}元可用该券
                            </div>
                        </div>
                    </div>
                </div>
                <div class="noDataBox" v-if="couponUselessList.length==0">
                    <img class="noDataImg" src="@/assets/images/default/no_coupon_img.png" alt="">
                    <div class="noDataTxt">暂无优惠券</div>
                </div>
            </div>
            <div class="completeBtn" v-if="btnItemIndex == 0" @click="completeBtn()">完成</div>
        </div>
        <!-- 配送地址选择 -->
        <div class="fixedLayer" v-if="isShowSelectBox" @click="hideSelectBox()"></div>
        <div class="setReginBox" v-if="isShowSelectBox">
            <div class="setReginHead">
                <div class="retreatBtn" @click="hideSelectBox()">
                    <img src="@/assets/images/common/left_black.png" alt="">
                </div>
                <div class="title">配送至</div>
            </div>
            <div class="selectBox">
                <div class="pitchUpTxt" v-if="regionData.recProvince" @click="queryProvince()">
                    {{regionData.recProvince}}</div>
                <div class="pitchUpTxt city" v-if="regionData.recCity" @click="getCityList(regionData.recProvince)">
                    {{regionData.recCity}}</div>
                <div class="pitchUpTxt district" v-if="regionData.recDistrict"
                     @click="queryDistrict(regionData.recCity)">{{regionData.recDistrict}}</div>
                <div class="pitchUpTxt district" v-if="regionData.recAreaName"
                     @click="getAreaList(regionData.recDistrict)"></div>
                <div class="plaseSelect" v-if="!regionData.recAreaName">
                    <div class="selectTxt">请选择</div>
                    <div class="signBox">
                        <span class="sign"></span>
                    </div>
                </div>
            </div>
            <div ref="adressSelectContent" class="adressSelectContent" scrollbar-y='false' delegate-handle="myScroll" has-bouncing='false'>
                <div class="selectItem" v-if="selectDataIndex==0" v-for="(selectItem,i) in selectData"
                     :class="{'pitchUpItem':selectIndex == i}" @click="selectCity(i)">
                    <span>{{selectItem.provinceName}}</span>
                    <img src="@/assets/images/mall/user_addressList_icon_choice.png" alt="">
                </div>
                <div class="selectItem" v-if="selectDataIndex==1" v-for="(selectItem,i) in selectData"
                     :class="{'pitchUpItem':selectIndex == i}" @click="selectCity(i)">
                    <span>{{selectItem.cityName}}</span>
                    <img src="@/assets/images/mall/user_addressList_icon_choice.png" alt="">
                </div>
                <div class="selectItem" v-if="selectDataIndex==2" v-for="(selectItem,i) in selectData"
                     :class="{'pitchUpItem':selectIndex == i}" @click="selectCity(i)">
                    <span>{{selectItem.districName}}</span>
                    <img src="@/assets/images/mall/user_addressList_icon_choice.png" alt="">
                </div>
                <div class="selectItem" v-if="selectDataIndex==3" v-for="(selectItem,i) in selectData"
                     :class="{'pitchUpItem':selectIndex == i}" @click="selectCity(i)">
                    <span>{{selectItem.areaName}}</span>
                    <img src="@/assets/images/mall/user_addressList_icon_choice.png" alt="">
                </div>
            </div>
        </div>
        <!-- 超出限购弹窗 -->
        <div class="outSaleAlert" v-if="isShowoutSaleAlert" @click="hideoutSaleAlert()">
            <div class="outSaleAlertContent">

                <div class="outSaleAlertTopBox">
                    <div class="outSaleListBox" v-if="overStoreList.length>0">

                        <div class="outSaleListTitle">以下商品促销库存不足，现需按如下价格购买，请重新确认</div>

                        <div class="outSaleListGoodsBox" v-for="(skuItem,i) in overStoreList">
                            <img class="outSaleListGoodsImg" :src="skuItem.skuPicUrl" alt="">
                            <div class="outSaleListGoodsDetailBox">
                                <div class="outSaleListGoodsTitle">{{skuItem.spuName}}</div>
                                <div class="outSaleListGoodsPriceBox">
                                    <div class="outSaleListGoodsPrice">
                                        ￥{{skuItem.skuPrice}}
                                    </div>
                                    <div class="outSaleListGoodsNum">
                                        x{{skuItem.skuCount}}
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>

                    <div class="line" v-if="overStoreList.length>0&&overLimitList.length>0"></div>
                    <div class="outSaleListBox" v-if="overLimitList.length>0">

                        <div class="outSaleListTitle">以下商品已超出限购数量，现需按如下价格购买，请重新确认</div>

                        <div class="outSaleListGoodsBox" v-for="(skuItem,i) in overLimitList">
                            <img class="outSaleListGoodsImg" :src="skuItem.skuPicUrl" alt="">
                            <div class="outSaleListGoodsDetailBox">
                                <div class="outSaleListGoodsTitle">{{skuItem.spuName}}</div>
                                <div class="outSaleListGoodsPriceBox">
                                    <div class="outSaleListGoodsPrice">
                                        ￥{{skuItem.skuPrice}}
                                    </div>
                                    <div class="outSaleListGoodsNum">
                                        x{{skuItem.skuCount}}
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>

                </div>
                <div class="outSaleButton" @click="hideoutSaleAlert()">
                    我知道了
                </div>

            </div>



        </div>
    </div>
</template>
<script>
    /*
    一、首次进入
    1.通过上一页面传值的店铺列表请求最新sku信息（modifySkuInfo），通过返回参数计算页面显示金额。同步校验收货地址（如果收货地址存在），如果非自提显示商品是否配送
    2.使用最新sku信息请求收货地址和自提信息及推荐优惠券
    3.计算可用积分金额并请求可用积分
    二、选择收货地址后返回本界面
    1.用户点击地址栏，跳转收货地址页
    2.返回支付页后，如果收货地址不为空，校验收货地址并重置积分和优惠券金额，校验后请求可用积分接口

    三、支付
    1.用户选择积分和优惠券之后，计算实际支付金额
    2.点击立即支付时请求支付订单参数，然后弹出支付方式
    3.用户选择取消时，弹出提示框是否放弃，放弃跳转订单列表，继续支付弹出支付方式
    4.用户选择支付方式后调用支付sdk，跳转支付app
    5.用户返回信我家时，监听收到支付完成消息，调用查询订单接口查看支付情况，支付成功跳转支付成功页，支付失败跳转订单列表页


    三、优惠券
    1.点击店铺列表下方的优惠券按钮时请求优惠券（可用和不可用）
    2.选择优惠券后重置积分并获取最新可用积分（可用积分为自营店铺总金额减去自营店铺优惠券金额）
    */
    import { mapState } from "vuex";
    import { Toast, Dialog } from "vant";
    import { storage } from '../../utils/storage';
    import NavigationBar from '@/common/currency/NavigationBar.vue';
    import {
        modifySkuInfoService,
        createOrderService,
        orderConfirmService,
        queryUsefulListService,
        changeRecAddrService,
        orderDetailService,
        getUseOrderLimitService,
        quickAddRecAddrService,
        unionOrderPrepayService
    } from '../../services/productOrderService.js';
    import {
        delRecAddrService,
        queryProvinceService,
        getCityListService,
        queryDistrictService,
        getVirtualAreaCodesService,
        getAreaListService
    } from '../../services/shippingAddressService.js';




    export default {
        name: 'orderPayNew',
        components: { NavigationBar },
        data() {
            return {
                goodsData: {},//页面传值
                shopList: [],//店铺列表
                pickupShopList: [],//自提店铺列表
                payParam: {
                    payWay: ""
                },//订单支付参数
                integralDeduction: 0,//按钮加减得积分
                pointAviable: 0,//最多可扣积分
                pointAviablePersent: "0.00",//最多可扣积分对应抵扣金额
                integralAll: false,//是否全部积分抵扣
                bill_amount: 0.00,//订单金额
                billCodePoint: 0.00,//订单实付金额
                canUsePointAmount: 0.00,//可用积分的金额
                recAddrListData: [],//地址列表数据
                recAddr: {},//当前收货地址
                slidesCount: false,
                isAllowPay: false,
                isAllowPayPickup: false,
                deliveryMode: "1",//支持配送类型，1仅配送，2自提+配送，3仅自提
                deliveryModeDesc: "网络请求中，请稍后",
                pickup: {},//自提信息
                coupon: {
                    "shopCode": null,
                    "couponCode": null,
                    "couponTotal": "0",
                },//优惠券
                isSelectBool: false,// 显示顶部选择列表 true配送 false自提
                isCouponBool: true,// true 使用优惠券 false未使用优惠券
                ownerName: "",//自提人姓名
                ownerPhone: "",//自提人电话
                selectCouponIndex: -1,//优惠券选中索引
                isShowCoverUp: false,//是否显示优惠券列表弹窗
                payWindowShow: false,//支付宝微信弹窗
                isWechart: false,//支付选择
                isAlipay: false,//支付选择
                isAcppay:false,//云闪付支付选择
                acpPayTag:'',//满减标签
                isAcpPayTag:false,//是否有满减标签
                isShowPopUp: false,
                isSaveOrder: false,//支付标记
                isShowSelectBox: false,//是否显示选择城市盒子 false 隐藏  true 显示
                recName: "",  //收货人
                recPhone: "", //手机号码
                regionData: {}, //收货地区
                defaultRegionData: {},
                recAddressDetail: "",//详细地址
                selectDataIndex: 0, // 0 获取省 1 市 2 区 3 小区
                recAddrItem: {},
                selectData: [],//存储选择列表数据
                isShowPlaceHolder: false,//键盘弹出时底部占位
                showPicker: false,//配送时间选择弹窗
                showOutPicker: false,//缺货选择弹窗
                columns: [],//配送时间选择器数据源
                outColumns: [],//缺货选择器数据源
                pickerIndex: 0,//配送时间选择器索引
                outPickerIndex: 0,//缺货选择索引
                nostoreStringList: [],//缺货数据源数组
                nostoreChoiceList: [],//缺货数据源索引数组
                nostoreString: "其他商品继续配送，缺货商品退款",
                nostoreChoice: 1,
                hasSecksellGoods: false,
                defaultImg: 'this.src="' + require('../../assets/images/default/default_picture.png') + '"',
                themeCurrent: 0,
                virtualAreaCodes: [],//虚拟小区集合
                isVirtualArea: false,
                /*大字模式*/
                bindThemeL: this.$curTheme == 0 ? false : true,
                isShowoutSaleAlert:false,//超出限购弹窗
                overStoreList:[],//超库存的SKU列表
                overLimitList:[],//超限购的SKU列表
            }
        },
        mounted() {
            this.nostoreStringList = ['其他商品继续配送，缺货商品退款', '缺货时电话与我沟通'];
            this.nostoreChoiceList = [1, 2];
            if (this.$devicePlatform == 'android') {
                let el = document.querySelector(".userCouponImg");
                if (el) {
                    el.style.paddingTop = '1px';
                }
                let el1 = document.querySelector(".storeImg");
                if (el1) {
                    el1.style.marginTop = '2px';
                }
            }

            document.querySelector(".orderPayNew").addEventListener('touchmove', function (e) {
                if (document.activeElement)
                    document.activeElement.blur();
            }, { passive: false });
        },
        activated() {
            this.themeCurrent = this.$curTheme;
            //添加键盘监听,让第一响应者滚动到键盘上方显示
            window.addEventListener("native.keyboardshow", this.moveInputIntoView);
            window.addEventListener("native.keyboardhide", this.resetInputIntoView);
            if (this.$route.params.goodsData) {
                this.clearData();
                this.defaultRegionData.recAreaName = this.$userInfo.AREA_NAME;
                this.defaultRegionData.recAreaCode = this.$userInfo.AREA_CODE;
                if (this.$userInfo.BUILD_NAME) {
                    this.recAddressDetail = this.$userInfo.BUILD_NAME + this.$userInfo.ROOM_NAME;
                }
                this.goodsData = this.$route.params.goodsData;
                this.shopList = this.goodsData.goodsList;
                this.$userInfo.PRODUCT_BILL_CODE = "";//记录订单号码


                //获取自提信息

                let pickupData = storage.getStorage("pickupData");

                if (!pickupData.$s_code_$exp) {
                    this.ownerName = storage.getStorage("pickupData").pickupName;
                    this.ownerPhone = storage.getStorage("pickupData").pickupPhone;
                }
                this.queryVirtualAreaCodes();//查询虚拟小区集合
                //拉取最新SKU信息
                this.modifySkuInfoFun();
            }
            this.selectCouponIndex = -1;
            //!获取缓存中的收货信息
            let recAddrTemp = storage.getStorage("recItem");
            if (!recAddrTemp.$s_code_$exp) {
                this.recAddr = storage.getStorage("recItem");
                this.recName = this.recAddr.recName;
                this.recPhone = this.recAddr.recPhone;
                this.changeRecAddr();
                this.pointAviablePersent = "0.00";
                this.integralDeduction = 0;
                this.integralAll = false;
                this.isShowCoverUp = false;
                storage.deleteStorage("recItem");
            }
            this.isShowCoverUp = false;
        },
        beforeRouteLeave(to, from, next) {
            //移除键盘监听,让第一响应者滚动到键盘上方显示
            window.removeEventListener("native.keyboardshow", this.moveInputIntoView);
            window.removeEventListener("native.keyboardhide", this.resetInputIntoView);
            if (to.name == "shippingAddressList" || to.name == "setAddress" || to.name == "readTextPage") {

            } else {
                /* 取消监听，避免重复监听 */
                if (this.$devicePlatform != "pc") {
                    window.broadcaster.removeEventListener("newPayBack", this.payCallBack);
                }
                this.clearData();
            }
            next();
        },
        computed: mapState({

            placeholderStyle: function () {
                let hv = this.$stbHeight + this.$footBarHeight + 135 + 'px !important'; //68px
                return { height: hv };
            },
            eventBackButtonFlag: state => state.goBack.eventBackButtonFlag
        }),
        watch: {



            /*监控到物理返回键点击状态*/
            eventBackButtonFlag(val) {
                if (val) {
                    this.$store.dispatch('setEventBackButtonFlag', false);

                    if (this.isShowSelectBox) {
                        this.isShowSelectBox = false;
                    } else if (this.isShowCoverUp) {
                        this.isShowCoverUp = false;
                    } else if (this.showPicker) {
                        this.showPicker = false;
                    } else if (this.showOutPicker) {
                        this.showOutPicker = false;
                    } else if (this.payWindowShow) {
                        this.closePopup();
                    } else if (this.isShowoutSaleAlert) {
                        this.isShowoutSaleAlert = false;
                    } else {
                        this.$router.go(-1);
                    }
                }
            }
        },
        filters: {
            threshold(data) {
                return data >= 10000 ? Math.floor(data / 10000 * 100) / 100 + '万' : data
            }
        },
        methods: {
            moveInputIntoView() {
                this.isShowPlaceHolder = true;
                // if (document.activeElement.id == "remarkTextarea") {
                //     // this.$nextTick(() => {
                //     //     document.activeElement.scrollIntoView();
                //     // })
                //     this.$nextTick(() => {
                //         var actualTop = document.activeElement.offsetTop;
                //         console.log("document.activeElement.offsetTop");
                //         console.log(document.activeElement.offsetTop);
                //         this.$refs.orderPayNewScroll.scrollTop = actualTop;
                //     });
                // }

                if (document.activeElement.id == "point_num"||document.activeElement.id == "remarkTextarea") {
                    // this.$nextTick(() => {
                    //     document.activeElement.scrollIntoView();
                    // })
                    this.$nextTick(() => {
                        var actualTop = document.activeElement.parentNode.parentNode.parentNode.offsetTop;
                        console.log("document.activeElement.actualTop");
                        console.log(actualTop);
                        this.$refs.orderPayNewScroll.scrollTop = actualTop;
                    });
                }  
            },
            //键盘收回事件
            resetInputIntoView(e) {

                this.isShowPlaceHolder = false;
                this.changePointNum();
            },
            pickupFocus() {
                this.$refs.orderPayNew.parentElement.parentElement.parentElement.style.position = 'Fixed';
            },
            pickupBlur() {
                this.$refs.orderPayNew.parentElement.parentElement.parentElement.style.position = '';
            },
            // 获取虚拟小区
            queryVirtualAreaCodes: function () {
                getVirtualAreaCodesService().then(res => {
                    if (res.resultCode == 0) {
                        this.virtualAreaCodes = res.data;

                        if (this.virtualAreaCodes.indexOf(this.$userInfo.AREA_CODE) !== -1) {
                            this.isVirtualArea = true;
                            this.defaultRegionData.recAreaName = "请选择省/市/区";
                            this.defaultRegionData.recAreaCode = "";
                            this.recAddressDetail = "";
                        }else{
                            this.isVirtualArea = false;
                        }
                    }
                    console.log(res)
                }).catch(error => {
                })
            },
            //拉取最新SKU信息
            modifySkuInfoFun: function () {
                modifySkuInfoService(this.shopList).then(res => {

                    if (res.resultCode == "0") {
                        this.shopList = res.data;
                        //计算订单总金额，生成spu列表

                        for (var i = 0; i < this.shopList.length; i++) {
                            //单个店铺总金额
                            this.shopList[i].shopTotal = 0;
                            for (var j = 0; j < this.shopList[i].skuList.length; j++) {
                                if (this.shopList[i].skuList[j].activitySpuId) {

                                    this.hasSecksellGoods = true;
                                }
                                //计算单个商品金额
                                this.shopList[i].skuList[j].skuTotal = Math.round(this.shopList[i].skuList[j].skuPrice * this.shopList[i].skuList[j].skuCount * 100) / 100;
                                //计算单个店铺
                                this.shopList[i].shopTotal += this.shopList[i].skuList[j].skuTotal;
                                this.shopList[i].remark = "";

                            }
                            //计算订单金额
                            this.bill_amount += this.shopList[i].shopTotal;
                            if (this.shopList[i].shopType == "01") {
                                //自营店铺可用积分金额
                                this.canUsePointAmount += this.shopList[i].shopTotal;
                            }
                            this.shopList[i].shopTotal = this.shopList[i].shopTotal.toFixed(2);
                        }
                        this.billCodePoint = this.bill_amount.toFixed(2);
                        this.$forceUpdate();
                        //获取默认收货地址及自提信息
                        this.orderConfirm();
                    } else {
                        Dialog.alert({
                            message: res.msg
                        }).then(() => {
                            this.goBack();
                        });
                    }

                }).catch(error => {

                    Dialog.alert({
                        message: '网络异常，请稍后重试！'
                    }).then(() => {
                        this.goBack();
                    });



                })
            },
            // 显示选择城市盒子
            showSelectBox: function () {
                this.isShowSelectBox = true;
            },
            // 隐藏选择城市盒子
            hideSelectBox: function () {
                this.isShowSelectBox = false;
            },

            //获取默认收货地址和自提点
            orderConfirm: function () {

                var data = {
                    "ownerCode": this.$userInfo.OWNER_CODE,
                    "areaCode": this.$userInfo.AREA_CODE,
                    "shopList": this.shopList,
                    "orderSource": this.goodsData.orderSource//订单来源0购物车，1订单列表/详情，2秒杀
                }
                orderConfirmService(data).then(res => {
                    if (res.resultCode == "0") {
                        this.deliveryMode = res.data.deliveryMode;
                        this.deliveryModeDesc = res.data.deliveryModeDesc;
                        if (res.data.deliveryMode == "4") {
                            Dialog.alert({
                                message: this.deliveryModeDesc
                            }).then(() => {
                                this.goBack();
                            });
                        }


                        if (res.data.deliveryMode == "2" || res.data.deliveryMode == "3") {
                            //支持自提
                            this.isSelectBool = false;
                            this.pickup = res.data.recPickupDefault;
                            if (res.data.couponPickupDefault.couponTotal != 0) {
                                this.couponPickupDefault = res.data.couponPickupDefault;
                                this.coupon = res.data.couponPickupDefault;
                            } else {
                                this.coupon = {
                                    "shopCode": null,
                                    "couponCode": null,
                                    "couponTotal": "0",
                                }
                                this.couponPickupDefault = {
                                    "shopCode": null,
                                    "couponCode": null,
                                    "couponTotal": "0",
                                }
                            }
                            this.pickupShopList = res.data.pickupShopList;


                            for (var i = 0; i < this.pickupShopList.length; i++) {
                                //单个店铺总金额
                                this.pickupShopList[i].shopTotal = 0;
                                for (var j = 0; j < this.pickupShopList[i].skuList.length; j++) {

                                    //计算单个商品金额
                                    this.pickupShopList[i].skuList[j].skuTotal = Math.round(this.pickupShopList[i].skuList[j].skuPrice * this.pickupShopList[i].skuList[j].skuCount * 100) / 100;
                                    //计算单个店铺
                                    this.pickupShopList[i].shopTotal += this.pickupShopList[i].skuList[j].skuTotal;
                                    this.pickupShopList[i].remark = "";

                                }
                                if (this.pickupShopList[i].delivery != "0" && this.pickupShopList[i].delivery) {
                                    this.pickupShopList[i].shopDeliveryTitle = "运费" + this.pickupShopList[i].delivery + "元";
                                    this.bill_amount += parseFloat(this.pickupShopList[i].delivery);
                                    this.pickupShopList[i].shopTotal += parseFloat(this.pickupShopList[i].delivery);

                                } else {
                                    this.pickupShopList[i].shopDeliveryTitle = "包邮";
                                }
                                this.pickupShopList[i].shopTotal = this.pickupShopList[i].shopTotal.toFixed(2);
                            }
                            //设置是否允许支付标记
                            if (0 == res.data.pickupResult) {
                                this.isAllowPayPickup = false;
                            } else {
                                this.isAllowPayPickup = true;
                            }
                            //设置商品显示是否配送
                            for (var i = 0; i < this.pickupShopList.length; i++) {
                                this.pickupShopList[i].noDistribution = false;
                                for (var j = 0; j < this.pickupShopList[i].skuList.length; j++) {
                                    this.pickupShopList[i].skuList[j].noDistribution = false;
                                    if (res.data.pickupSpuList) {
                                        if (res.data.pickupSpuList.length > 0) {
                                            for (var k = 0; k < res.data.pickupSpuList.length; k++) {
                                                if (res.data.pickupSpuList[k] == this.pickupShopList[i].skuList[j].spuCode) {
                                                    this.pickupShopList[i].skuList[j].noDistribution = true;
                                                    this.pickupShopList[i].noDistribution = true;
                                                }
                                            }
                                        }
                                    }
                                }
                            }



