            // 获取用户可用积分
            getUseOrderLimitFn: function () {


                var canUsePointMoney = this.canUsePointAmount;
                for (var i = 0; i < this.shopList.length; i++) {
                    if ((this.coupon.shopCode == this.shopList[i].shopCode) && this.shopList[i].shopType == "01") {
                        canUsePointMoney = this.canUsePointAmount - this.coupon.couponTotal;
                    }

                }

                var data = {
                    ownerCode: this.$userInfo.OWNER_CODE,
                    money: canUsePointMoney
                }
                console.log(data);
                getUseOrderLimitService(data).then(res => {
                    console.log(res);
                    if (res.code == 200) {//true表示返回成功
                        this.pointAviable = Math.floor(res.data / 100) * 100;
                    } else {
                        this.pointAviable = 0;
                    }
                }).catch(error => {
                })
            },
            //显示优惠券列表
