            setBtnItemIndex: function (index) {
                this.btnItemIndex = index;
                this.$forceUpdate();
            },
            //顶部切换选择按钮
            switchBtn: function (isBool) {

                if (this.deliveryMode == 1 || this.deliveryMode == 3) {
                    Toast(this.deliveryModeDesc);
                    return;
                }
                if (isBool == true) {
                    this.coupon = this.couponAddrDefault;
                    //自提切换收货校验
                    // if (this.recAddr != null) {
                    //     this.changeRecAddr();
                    // }
                } else {
                    this.coupon = this.couponPickupDefault;
                }

                //重置抵扣积分和抵扣钱数
                this.pointAviablePersent = "0.00";
                this.integralDeduction = 0;
                this.integralAll = false;
                //获取可用积分
                this.getUseOrderLimitFn();

                if (this.coupon && this.coupon.couponTotal) {
                    this.billCodePoint = (parseFloat(this.bill_amount) - parseFloat(this.pointAviablePersent) - parseFloat(this.coupon.couponTotal)).toFixed(2);//实付金额
                }

                this.isSelectBool = isBool;
            },
