            //获取可用优惠券列表
            queryUsefulList: function () {
                if (!this.isSelectBool) {
                    var data = {
                        ownerCode: this.$userInfo.OWNER_CODE,
                        shopList: this.shopList,
                        areaCode: this.$userInfo.AREA_CODE
                    }
                } else {
                    var data = {};
                    if (this.recAddr) {
                        data = {
                            "areaCode": this.recAddr.recAreaCode,
                            "shopList": this.shopList,
                            "ownerCode": this.$userInfo.OWNER_CODE,
                        }
                    } else {
                        data = {
                            "areaCode": this.defaultRegionData.recAreaCode,
                            "shopList": this.shopList,
                            "ownerCode": this.$userInfo.OWNER_CODE,
                        }
                    }

                }

                queryUsefulListService(data).then(res => {
                    if ("0" == res.resultCode) {
                        this.isShowCoverUp = true;
                        this.btnItemIndex = 0;
                        this.couponUsefulList = res.data.couponUsefulList;
                        for (var i = 0; i < this.couponUsefulList.length; i++) {
                            this.couponUsefulList[i].seeRange = false;
                            if (this.couponUsefulList[i].couponCode == this.coupon.couponCode) {
                                this.selectCouponIndex = i;
                            }
                        }
                        this.couponUselessList = res.data.couponUselessList
                        for (var i = 0; i < this.couponUselessList.length; i++) {
                            this.couponUselessList[i].seeRange = false;
                        }
                    } else {
                        Toast(res.msg);
                    }
                }).catch(error => {
                });

            },
            //查看优惠券的适用范围
            changeSeeRange: function (index, typeFlag) {
                if (typeFlag == 0) {
                    this.couponUsefulList[index].seeRange = !this.couponUsefulList[index].seeRange;
                } else {
                    this.couponUselessList[index].seeRange = !this.couponUselessList[index].seeRange;
                }
                this.$forceUpdate();
            },
            //选中的可用优惠券
            selectCouponItem: function (index) {
                if (this.selectCouponIndex == index) {
                    this.selectCouponIndex = -1;
                } else {
                    this.selectCouponIndex = index;
                }
            },
            //选中优惠券后点击完成按钮
            completeBtn: function () {
                if (this.selectCouponIndex == -1) {
                    this.coupon = {
                        couponCode: "",
                        couponTotal: "0",
                        shopCode: ""
                    }
                    this.isCouponBool = false;
                    this.billCodePoint = (this.bill_amount).toFixed(2);
                } else {
                    this.isCouponBool = true;
                    this.coupon = this.couponUsefulList[this.selectCouponIndex]
                    this.billCodePoint = (this.bill_amount - parseFloat(this.coupon.couponTotal)).toFixed(2);
                }
                //重置抵扣积分和抵扣钱数
                this.pointAviablePersent = "0.00";
                this.integralDeduction = 0;
                this.integralAll = false;

                //获取可用积分
                this.getUseOrderLimitFn();
                this.isShowCoverUp = false;
            },

