            checkAddresBeforecreateOrder: function () {


                if (this.billCodePoint == '0.00') {
                    this.$toast("实付金额不能为0");
                    return;
                }

                if (this.isSelectBool == true) { //送货上门
                    // if (this.recAddr == null) {
                    //     this.$toast("请选择配送地址");
                    //     return;
