                                            this.pickupShopList[i].skuList[j].activitySpuId = null;
                                        }
                                        //计算单个商品金额
                                        this.pickupShopList[i].skuList[j].skuTotal = Math.round(this.pickupShopList[i].skuList[j].skuPrice * this.pickupShopList[i].skuList[j].skuCount * 100) / 100;
                                        //计算单个店铺
                                        this.pickupShopList[i].shopTotal += this.pickupShopList[i].skuList[j].skuTotal;
                                        this.pickupShopList[i].remark = "";

                                    }
                                    if (this.pickupShopList[i].delivery != "0" && this.pickupShopList[i].delivery) {
                                        this.pickupShopList[i].shopTotal += parseFloat(this.pickupShopList[i].delivery);
                                    }
                                    //计算订单金额
                                    this.bill_amount += this.pickupShopList[i].shopTotal;
                                    if (this.pickupShopList[i].shopType == "01") {
                                        //自营店铺可用积分金额
                                        this.canUsePointAmount += this.pickupShopList[i].shopTotal;
                                    }
                                    this.pickupShopList[i].shopTotal = this.pickupShopList[i].shopTotal.toFixed(2);
                                }
                                if (res.data.couponDefault.couponTotal != 0) {
                                    this.coupon = res.data.couponDefault;
                                    this.couponAddrDefault = res.data.couponDefault;
                                } else {
                                    this.coupon = {
                                        "shopCode": null,
                                        "couponCode": null,
                                        "couponTotal": "0",
                                    }
                                }

                                this.billCodePoint = (this.bill_amount - parseFloat(this.coupon.couponTotal)).toFixed(2);


                                for (var i = 0; i < this.shopList.length; i++) {
                                    //单个店铺总金额
                                    this.shopList[i].shopTotal = 0;
                                    for (var j = 0; j < this.shopList[i].skuList.length; j++) {

                                        if (res.data.overStoreList[k].skuNo == this.shopList[i].skuList[j].skuNo) {

                                            this.shopList[i].skuList[j].skuPrice = res.data.overStoreList[k].skuPrice;
                                            this.shopList[i].skuList[j].activitySpuId = null;
                                        }
                                        //计算单个商品金额
                                        this.shopList[i].skuList[j].skuTotal = Math.round(this.shopList[i].skuList[j].skuPrice * this.shopList[i].skuList[j].skuCount * 100) / 100;
                                        //计算单个店铺
                                        this.shopList[i].shopTotal += this.shopList[i].skuList[j].skuTotal;
                                        this.shopList[i].remark = "";

                                    }
                                    if (this.shopList[i].delivery != "0" && this.shopList[i].delivery) {
                                        this.shopList[i].shopTotal += parseFloat(this.shopList[i].delivery);
                                    }
                                    //计算订单金额
                                    if (this.shopList[i].shopType == "01") {
                                        //自营店铺可用积分金额
                                        // this.canUsePointAmount += this.shopList[i].shopTotal;
                                    }
                                    this.shopList[i].shopTotal = this.shopList[i].shopTotal.toFixed(2);
                                }
                            }
                        }
                        //获取可用积分
                        this.getUseOrderLimitFn();

                        this.isShowoutSaleAlert = true;
                        this.$forceUpdate();

                    }
                    else {
                        this.payParam = {};
                        this.$userInfo.PRODUCT_BILL_CODE = "";
                        // var msg = res.msg;
                        // Dialog.alert({
                        //     message: msg
                        // }).then(() => {
                        //     // 确定
                        // });
                        Toast(res.msg);
                    }
                })
                    .catch(error => {
                        this.isSaveOrder = false;
                    });
            },




            whichPay: function (x, money, p) {
                if (1 == p.IS_ALI_PAY) {
                    this.isAlipay = true;
                } else {
                    this.isAlipay = false;
                };
                if (1 == p.IS_WX_PAY) {
                    this.isWechart = true;
                } else {
                    this.isWechart = false;
                };
                if (1 == p.IS_ACP_PAY) {
                    this.isAcppay = true;
                } else {
                    this.isAcppay = false;
                };
                if (p.acpPayTag != "" && p.acpPayTag != undefined &&p.acpPayTag != null){
                    this.acpPayTag = p.acpPayTag;
                    this.isAcpPayTag = true;
                }

                this.payWindowShow = true;

            },
            closePopup(isShowWindow = false) {
                if (isShowWindow == true) {
                    this.payWindowShow = false;
                    this.isShowPopUp = true;
                    return;
                } else {
                    this.payWindowShow = false;
                    if (!this.isShowPopUp) {
                        Dialog.confirm({
                            title: '确定放弃支付该订单吗？',
                            confirmButtonText: "确认离开",
                            cancelButtonText: "继续支付"
                        }).then(() => {
                            // 跳转订单列表
                            this.goMyOrderList();
                        }).catch(() => {
                            // on cancel
                            this.payWindowShow = true;
                        });
                    }
                }

            },
            /*银联下单支付*/
            alipay: function (index) {//x订单编码//y支付金额,index 0 微信 1 支付宝 p 支付参数
                let _this=this;
                if (index===2){
                    let pram = {
                        'merOrderId':this.payParam.merOrderId,
                        'payType':2
                    }
                    unionOrderPrepayService(pram).then(res =>{
                        this.closePopup(true);
                        if (res.resultCode!=="0"){
                            Dialog.alert({
                                message: res.msg
                            }).then(() => {
                            });
                            return false;
                        }
                        let unipay = cordova.plugins.LantuUnionPay;
                        if (this.$devicePlatform == "android"){
                            unipay.pay({tn:res.data.tn,payMode:this.payParam.acpGatewayType},(success)=>{
                                console.log("支付成功回调"+JSON.stringify(success));
                                _this.getOrderDetail();
                            },(error)=>{
                                console.log("支付失败回调"+JSON.stringify(error));
                                _this.getOrderDetail();
                            });
                        }else {
                            unipay.isUnionAppInstalled((success)=>{
                                if (this.$devicePlatform != "pc") {
                                    window.broadcaster.addEventListener("newPayBack", this.payCallBack);
                                }
                            },(error)=>{
                            });
                            unipay.pay({tn:res.data.tn,payMode:this.payParam.acpGatewayType},(success)=>{
                                console.log("支付成功回调"+JSON.stringify(success));
                                _this.getOrderDetail();
                            },(error)=>{
                                console.log("支付失败回调"+JSON.stringify(error));
                                _this.getOrderDetail();
                            });
                        }
                    }).catch(error => {
                        this.closePopup(true);
                        Dialog.alert({
                            message: '网络异常，请稍后重试！'
                        }).then(() => {
                        });
                        console.log(error);
                    });
                }else {
                    this.payParam.payWay = index;
                    //监听ios支付结果 检测当前订单状态
                    this.closePopup(true);

                    if (this.$devicePlatform != "pc") {
                        window.broadcaster.addEventListener("newPayBack", this.payCallBack);
                    }
                    window.broadcaster.fireNativeEvent("ionicToNative", {
                        DATA: this.payParam,
                        FLAG: '567'
                    }, function () {
                    })
                }
            },
            //支付完成后回调
            payCallBack() {
                this.getOrderDetail();
            },


            // 支付完成后获取订单详情查询订单状态
            getOrderDetail: function () {
                if (this.$userInfo.PRODUCT_BILL_CODE != undefined && this.$userInfo.PRODUCT_BILL_CODE != "") {
                    var data = {
                        billNo: this.$userInfo.PRODUCT_BILL_CODE,
                        ownerCode: this.$userInfo.OWNER_CODE
                    }

                    this.closePopup(true);
                    // this.isShowPopUp = false;
                    // this.clearData();
                    orderDetailService(data).then(res => {
                        // this.isShowPopUp = true;
                        // this.clearData();
                        if ("0" == res.resultCode) {//true表示返回成功
                            if (res.data.billStatus == "02") {
                                //订单状态为已支付，跳转支付成功界面，
                                // this.clearData();
                                this.$router.push({ name: "paySuccess", params: { orderData: res.data } });
                                console.log("支付成功");
                            } else {
                                //其他情况跳转订单列表页
                                this.goMyOrderList();
                                console.log("支付失败");
                            }
                        } else {
                            //其他情况跳转订单列表页
                            this.goMyOrderList();
                            console.log("查询订单状态失败");
                        }

                    }).catch(error => {
                        //其他情况跳转订单列表页
                        this.goMyOrderList();
                        console.log("查询订单状态失败");
                    });
                } else {
                    this.goMyOrderList();
                    console.log("订单号丢失");
                }

            },

            //跳转收货地址新增
            addRecAddr: function () {
                var recAddrData = {
                    flag: 2
                }
                this.$router.push({ name: "setAddress", params: { data: recAddrData } });
            },


            //跳转收货地址选择列表
            goRecAddrList: function () {

                if (this.recAddr) {
                    this.$router.push({ name: "shippingAddressList", query: { flag: 2 } });

                } else {
                    var recAddrData = {
                        flag: 2
                    }
                    this.$router.push({ name: "setAddress", params: { data: recAddrData } });
                }


            },

            //支付失败跳转订单列表页
            goMyOrderList: function () {
                // this.clearData();
                this.$router.replace({ name: "myOrderList", query: { flag: 10, ifPayReturn: true } });
            },

            //点击送达时间
            deliveryClick: function (shopItem, index) {

                this.columns = [];
                this.showPicker = !this.showPicker;
                if (this.showPicker) {
                    this.pickerIndex = index;//记录配送时间选择器索引
                }

                this.columns = [
                    {
                        values: Object.keys(shopItem.deliveryDayData),
                        className: 'column1',
                        defaultIndex: 0
                    },
                    {
                        values: shopItem.deliveryDayData[(Object.keys(shopItem.deliveryDayData))[0]],
                        className: 'column2',
                        defaultIndex: 0
                    }
                ]
                //将选择器选项初始化
                this.$refs.deliveryPicker.setColumnIndex(0, 0);
                this.$refs.deliveryPicker.setColumnIndex(1, 0);
            },

            //点击配送时间完成按钮
            pickerOnConfirm: function (values, indexs) {

                if (this.isSelectBool) {
                    this.shopList[this.pickerIndex].deliveryDefaultString = values[0] + " " + values[1];
                    this.shopList[this.pickerIndex].deliveryDate = this.shopList[this.pickerIndex].deliveryDayList[indexs[0]].deliveryDate;
                    var timeAry = values[1].split("-");
                    this.shopList[this.pickerIndex].deliveryTimeStart = timeAry[0];
                    this.shopList[this.pickerIndex].deliveryTimeEnd = timeAry[1];
                    this.showPicker = false;
                    this.shopList[this.pickerIndex].deliveryNeedUpdata = "0";
                    this.pickerIndex = 0;
                } else {
                    this.pickupShopList[this.pickerIndex].deliveryDefaultString = values[0] + " " + values[1];
                    this.pickupShopList[this.pickerIndex].deliveryDate = this.pickupShopList[this.pickerIndex].deliveryDayList[indexs[0]].deliveryDate;
                    var timeAry = values[1].split("-");
                    this.pickupShopList[this.pickerIndex].deliveryTimeStart = timeAry[0];
                    this.pickupShopList[this.pickerIndex].deliveryTimeEnd = timeAry[1];
                    this.showPicker = false;
                    this.pickupShopList[this.pickerIndex].deliveryNeedUpdata = "0";
                    this.pickerIndex = 0;
                }



            },
            //配送时间变化
            pickerOnChange: function (picker, values) {
                if (this.isSelectBool) {
                    picker.setColumnValues(1, this.shopList[this.pickerIndex].deliveryDayData[values[0]]);

                } else {
                    picker.setColumnValues(1, this.pickupShopList[this.pickerIndex].deliveryDayData[values[0]]);

                }
            },

            //点击如遇缺货
            outClick: function () {

                this.outColumns = [];
                this.showOutPicker = !this.showOutPicker;
                // if (this.showOutPicker) {
                //     this.outPickerIndex = index;//记录配送时间选择器索引
                // }

                this.outColumns = [
                    {
                        values: this.nostoreStringList,
                        defaultIndex: 0
                    }
                ]

                // debugger;
                var index = 0;
                for (var m = 0; m < this.nostoreChoiceList.length; m++) {

                    if (this.nostoreChoiceList[m] == this.nostoreChoice) {
                        index = m;
                    }


                }
                //将选择器选项初始化
                this.$refs.outPicker.setColumnIndex(0, index);
            },

            //点击缺货完成按钮
            outPickerOnConfirm: function (value, index) {
                // debugger;
                this.nostoreString = value[0];

                this.nostoreChoice = this.nostoreChoiceList[index[0]];
                this.showOutPicker = false;
            },




            //输入积分数值
            changePointNum: function () {

                this.integralDeduction = this.integralDeduction.replace(/[^\d]/g, '')
                if (this.integralDeduction == "") {
                    this.integralDeduction = 0;
                    // return;
                }

                if (this.integralDeduction > this.pointAviable) {
                    this.integralDeduction = Math.floor(this.pointAviable / 100) * 100;
                    this.msg = '本单最高抵扣' + this.pointAviable + '积分';
                    this.$toast(this.msg);
                } else if (this.integralDeduction < 0) {
                    this.integralDeduction = 0;
                    this.msg = '抵扣积分不能为负数';
                    this.$toast(this.msg);
                } else {
                    if (this.integralDeduction % 100 != 0) {
                        this.msg = '抵扣积分为整百';
                        this.integralDeduction = Math.floor(this.integralDeduction / 100) * 100;
                        this.$toast(this.msg);
                    }
                }
                console.log(this.integralDeduction)
                this.pointAviablePersent = (this.integralDeduction / 100).toFixed(2);
                this.billCodePoint = (parseFloat(this.bill_amount) - parseFloat(this.pointAviablePersent) - parseFloat(this.coupon.couponTotal)).toFixed(2);//实付金额
            },

            //按钮加
            numberAdd: function (intergral) {
                if (!this.$userInfo.ROOM_CODE) {
                    Toast("积分不可用");
                    return;
                }
                if (intergral == 0) {
                    Toast("暂无可用积分");
                    return;
                }

                if (this.integralDeduction < intergral) {
                    if (this.integralDeduction + 100 <= intergral) {
                        this.integralDeduction = this.integralDeduction + 100;
                    }
                    else {
                        this.$toast('本次可用最高积分为' + this.integralDeduction);
                    }
                } else {

                    this.integralDeduction = intergral;
                    this.$toast('本次可用最高积分为' + this.integralDeduction);
                }
                this.pointAviablePersent = (this.integralDeduction / 100).toFixed(2);//最多可扣积分
                this.billCodePoint = (parseFloat(this.bill_amount) - parseFloat(this.pointAviablePersent) - parseFloat(this.coupon.couponTotal)).toFixed(2);//实付金额
            },

            //按钮减
            numberReduce: function () {
                if (!this.$userInfo.ROOM_CODE) {
                    return;
                }

                if (this.integralDeduction == 0) {
                    this.$toast("已经是最低了，不能在减少了哦！");
                    this.integralDeduction = 0;
                } else {
                    if (this.integralDeduction - 100 < 0) {
                        this.integralDeduction = 0;
                    } else {
                        this.integralDeduction = this.integralDeduction - 100;
                    }
                }
                this.pointAviablePersent = (this.integralDeduction / 100).toFixed(2);//最多可扣积分
                this.billCodePoint = (parseFloat(this.bill_amount) - parseFloat(this.pointAviablePersent) - parseFloat(this.coupon.couponTotal)).toFixed(2);//实付金额
            },

            //是否全部积分抵扣
            integralAllFn: function (intergrals) {
                if (!this.$userInfo.ROOM_CODE) {
                    this.integralAll = false;
                    Toast("积分不可用");
                    return;
                }
                console.log(intergrals / 100);
                if (intergrals == 0) {
                    Toast("暂无可用积分");
                    this.integralAll = false;
                    return;
                }


                console.log(this.integralAll);
                if (this.integralAll) {
                    this.integralDeduction = Math.floor(intergrals / 100) * 100;
                } else {
                    this.integralDeduction = 0;
                }
                this.pointAviablePersent = (this.integralDeduction / 100).toFixed(2);//最多可扣积分
                this.billCodePoint = (parseFloat(this.bill_amount) - parseFloat(this.pointAviablePersent) - parseFloat(this.coupon.couponTotal)).toFixed(2);//实付金额
            },

            //积分说明
            integralInstructionFn: function () {
                this.$router.push({
                    name: "readTextPage",
                    params: { pageType: 5 }
                });
            },


            //立即支付按钮
            goToPay: function () {
                //保存抵扣积分和实付金额,生成订单，并获取银联支付参数
                if ((!this.isAllowPay && this.isSelectBool) || (!this.isAllowPayPickup && !this.isSelectBool)) {
                    Toast("商品不在配送范围内，请修改购物车或收货地址");
                    return;
                };
                if (this.$userInfo.PRODUCT_BILL_CODE == "" || this.$userInfo.PRODUCT_BILL_CODE == undefined) {
                    //下单前校验保存收货地址
                    this.checkAddresBeforecreateOrder();
                } else {
                    //弹窗跳转订单列表
                    this.goMyOrderList();
                };
            },
            goBack: function () {
                console.log("goback/orderPayNew");
                this.$router.go(-1);
            },

            clearData: function () {

                this.goodsData = {};//页面传值
                this.shopList = [];//店铺列表
                this.pickupShopList = [];//自提店铺列表
                this.payParam.payWay = "";//订单支付参数
                this.integralDeduction = 0;//按钮加减得积分
                this.pointAviable = 0;//最多可扣积分
                this.pointAviablePersent = "0.00";//最多可扣积分对应抵扣金额
                this.integralAll = false;//是否全部积分抵扣
                this.bill_amount = 0.00;//订单金额
                this.billCodePoint = 0.00;//订单实付金额
                this.canUsePointAmount = 0.00;//可用积分的金额
                this.recAddrListData = [];//地址列表数据
                this.recAddr = {};//当前收货地址
                this.slidesCount = false;
                this.isAllowPay = false;
                this.isAllowPayPickup = false;
                this.deliveryMode = "1";//是否可以自提
                this.deliveryModeDesc = "网络请求中，请稍后";
                this.pickup = {};//自提信息
                this.coupon = {
                    "shopCode": null,
                    "couponCode": null,
                    "couponTotal": "0",
                };//优惠券
                this.isSelectBool = false;// 顶部选择列表 true配送 false自提
                this.isCouponBool = true;// true 使用优惠券 false未使用优惠券
                this.ownerName = "";//自提人姓名
                this.ownerPhone = "";//自提人电话
                this.selectCouponIndex = -1;//优惠券选中索引
                this.isShowCoverUp = false;//是否显示优惠券列表弹窗
                this.payWindowShow = false;//支付宝微信弹窗
                this.isWechart = false;//支付选择
                this.isAlipay = false;//支付选择
                this.isAcppay = false;//云闪付支付选择
                this.isShowPopUp = false;//
                this.isSaveOrder = false;
                this.isShowSelectBox = false;//是否显示选择城市盒子 false 隐藏  true 显示
                this.recName = "";  //收货人
                this.recPhone = ""; //手机号码
                this.regionData = {}; //收货地区
                this.defaultRegionData = {};//默认小区
                this.recAddressDetail = "";//详细地址
                this.selectDataIndex = 0; // 0 获取省 1 市 2 区 3 小区
                this.recAddrItem = {};
                this.selectData = [];//存储选择列表数据
                this.isShowPlaceHolder = false;//键盘弹出时底部占位
                this.showPicker = false;//时间选择弹窗
                this.columns = [];//配送时间选择器数据源
                this.outColumns = [];//缺货选择器数据源
                this.pickerIndex = 0;//配送时间选择器索引
                this.outPickerIndex = 0;//缺货选择器索引
                this.nostoreString = "其他商品继续配送，缺货商品退款";
                this.nostoreChoice = 1;
                this.showOutPicker = false;
                this.hasSecksellGoods = false;
                this.isVirtualArea = false;
                this.isShowoutSaleAlert = false;
            }



        }

    }



</script>


